#containerNoticias{
    display: flex;
    width: 99.1vw;
    height: auto;
    justify-content: start;
    align-items: center;
    flex-direction: column;
    background-color: #fff;
    overflow: hidden;

}
.itemNoticia{
    transition-duration: 200ms;
    cursor: pointer;
}
.itemNoticia:hover {
    transform: scale(1.1);
}

.itensNoticias{
    display:flex;
    width: 100vw;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
    
}
.itemNoticia{
    display: flex;;
    width: 300px;
    height: 300px;
    margin: 40px;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.containerTituloNoticias h1{
    color:#666;
}

#imagemNot1{
    /* background-image: url('../../Assets/mourao.jpg'); */
    background-size: 100% 100%;
    width: 300px;

    height: 200px;
}

#imagemNot2{
    /* background-image: url('../../Assets/corona.jpg'); */
    background-size: 100% 100%;
    width: 300px;

    height: 200px;
}

#imagemNot3{
    /* background-image: url('../../Assets/elon.jpg'); */
    background-size: 100% 100%;
    width: 300px;
    height: 200px;
}

#imagemNot4{
    /* background-image: url('../../Assets/carro.jpg'); */
    background-size: 100% 100%;;
    height: 200px;
}
.descricaoNoticias{
    color:#666;
    text-align: center;
    margin-top:7px;
    font-size: 11pt;
    text-align: justify;
    width: 100%;;
    height: 250px;
}
.dataNoticias{
    padding: 5px;
    color: #666;
}
.leiaMais{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100px;
    height: 30px;
    background-color: #e6d819;
    text-decoration: none;
    color: #08c56d;
    margin-bottom: 20px;
    transition-duration: 200ms;
}
.leiaMais:hover{
    transform: scale(1.1);
}
.linha{
    width: 150px;
    border: 1px solid  #fff;
   margin-top:10px
}
.containerTituloNoticias{
    display: flex;
    justify-content: center;
    flex-direction: row;
    align-items: center;
    color: #fff;
    margin-top: 5px;
}
.NotTitulo{
    font-size: 1.5rem;

}
.iconNoticias{
    width: 25px;
    height: 25px;
}
.containerTituloNoticias *{
    margin: 5px;
}