#Servicos{
    margin-bottom: 100px;
}
.containerServicos {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: auto;
}

.tituloServicos{
    display: flex;
    height: 100px;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    
}
.tituloServicos * {
    margin: 5px;
}
.tituloServicos h1{
    font-size: 1.5rem;
}

.superiorServicos{
    display: flex;
    flex-direction: row;
    list-style: none;
    flex-wrap: wrap-reverse;
    justify-content: center;
    align-items: center;
}
.superiorServicos * {
    margin: 10px;
    
}
.inferiorServicos{
    display: flex;
    flex-direction: row;
    list-style: none;
    flex-wrap: wrap-reverse;
    justify-content: center;
    align-items: center;
}
.inferiorServicos * {
    margin: 10px;
}
.itemServico{
    display: flex;
    width: 146px;
    height: 132px;
    border: solid 1px #01b561;
    justify-content: center;
    align-items: center;
    background: none;
    outline: none;
    font-weight: bold;
    flex-direction: column;
    cursor: pointer;
    transition-duration: 200ms;
    
}
.itemServico:hover{
    background-color:#01b561;
    color:#fff;
    transform: scale(1.1);
}

.iconsItem{
    width: 40px;
    height: 40px;
    color:#01b561;
    transition: transform .3s;

}
.itemServico:hover .iconsItem {
    color: #fff;
    transform: rotate(-25deg) skew(0deg);
  

}
.iconTitulo{
    width: 25px;
    height: 25px;
    color: #01b561;
}
.itemName{
    width: 146px;
    font-size: 9pt;
    font-weight: bold;
}