
.topo{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100vw;
    margin: 0;
    position: fixed;
    transition: 350ms linear;
    background-color: #fff;
    z-index: 1;




}

.logo {
    background-image: url(../../Assets/aciaglogo.png);
    width: 80px;
    height: 80px;
    background-size: 100% 100%;
    margin-left: 7%;

}


.menu ul{
    list-style: none;
    display: flex;
}

.menu ul li {
    margin: 25px;
    cursor: pointer;

 
}
.acessoRapido{
    display: flex;
    list-style: none;
    cursor: pointer;
}
.elementoMenu {
    font-size: 1rem;
    text-decoration: none;
    color: #01b561;
    font-weight: bold;
    background: none;
    border:none;
    outline: none;
    border: 1px solid transparent;
    padding: 5px;
}

.elementoMenu:hover {
    font-size: 1rem;
    text-decoration: none;
    color: #03723e;
    font-weight: bold;
    padding: 5px;;
    border-bottom: 1.5px solid #01b561;
    
}


.containerHeader{
    display: flex;
    background-color:#01b561;
    width: 30vw;
    height: 100px;
    justify-content: center;
    align-items:  center;
    transition: 350ms linear;
}
.containerHeader * {    
    text-decoration: none;
}
.containerLogin{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-left: 10px;
    border-left: 1px solid #fff;
}

.grandesItens {
    display: flex;
    text-decoration: none;
    width: 80px;
    height: 40px;
    border: solid 1px #fff;
    color:#f3e40d;
    font-size: 1rem;
    justify-content: center;
    align-items: center;
    /* background: #e4d724; */
    border: none;
    outline: none;
    flex-direction: column;
    transition-duration: 200ms;
 
    /* font-weight: bold; */
}
.grandesItens:hover {
    transform: scale(1.1) ;
    color: #d8cb10; 

}
.itensMenores{
    border: none;
    background: none;
    margin: 10px;
    color:#fff;
    font-size: 1rem;
    border: solid 1px transparent;
    transition-duration: 200ms;
}
.itensMenores:hover{
    /* color: rgb(207, 247, 222); */
    transform: scale(1.1)

}

.teste {
    height: 500vh;
}
#institucional{
    display: flex;
    justify-content: center;
    align-self: center;
}

#iconInst {
    width: 16px;
    height: 11px;
}

#listaInst{
    position: absolute;
    border: solid 1px rgb(226, 224, 224);
    z-index: 1;
}
#intitucional:hover #listaInst{
    display: flex;
}

#listaInst{
    display: none;
    position: absolute;
    list-style: none;
    color: #01b561;
    flex-direction: column;
    background-color:#fff;
    transition-duration: 200ms;
    box-shadow: 0px 0px 1px 1px rgb(240, 239, 239);

}
#cascataMenu{
    margin:15px;
    transition-duration: 200ms;


}
#cascataMenu:hover{
    transform: scale(1.1)
}

.upButton{
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;    
    width: 50px;
    height: 50px;
    bottom: 40px;
    right: 40px;
    border-radius:50%;
    background-color: #f3e40d;
    box-shadow: 1px 1px 1px 1px  rgb(180, 176, 176);
    transition-duration: 200ms;

}

.upButton:hover{
    transform: scale(1.2);
    color:#fff;
}
/* Media Query para responsividade */

#checkbox-menu{
    display: none;
    position: absolute;
    opacity: 0;
  }
  #analiseCreditoCascata:hover #subMenuAnalise{
    display:flex;
    transition: linear;
  }
  #subMenuAnalise{
      display: none;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      position: absolute;
      width: 200px;
      height: 70px;
      background-color: #fff;
      color: #01b561 ;
      list-style: none;
      box-shadow: 0px 0px 1px 1px rgb(202, 202, 202);
      transition-duration: 200ms ;

  }
#subMenuAnalise *{
    margin: 7px;
    transition-duration: 200ms ;
}
#subMenuAnalise *:hover{
    transform: scale(1.1)
}

  
