#spc{
    background-image: url('../../Assets/spc-brasil-logo.png') ;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    width: 70px;
    height:25px;
}
@media(max-width: 1530px){
    .containerLogin{

        border-left: none;
    }
}

@media(max-width: 1250px){
    .containerHeader  {
        display: none;
       
   
    }

    .containerHeader a {
        display: none;
    }


    .topo{
        margin-top: 10px;
        justify-content: space-around;
    }
    



}

@media(max-width: 1100px) {
    .logo {
        display: none;
    }
    .topo{
        justify-content: center;
    }

    .menu{
        margin-left: 13vw;
    }

}

@media(max-width: 800px){

    
    .topo{
        margin-top: 0;
        background-color:#01b561;
        display: flex;
        justify-content: center;
        width: 100vw;
        height: 100px;
        transition: 500ms linear;
    }

    .menu ul{
       display: none;
    }

    .logo{
        display: flex;
        color: #fff;
        font-weight: bold;
        margin-left:15%;
        font-size: 17pt;
        align-items: center;
 
    }
    /* Início botão hamburguer */
  label{
    cursor: pointer;
    position: absolute;
    display: block;
    height: 22px;
    width: 30px;
    right: 20px;
  }
  
  label span{
    position: absolute;
    display: block;
    height: 5px;
    width: 100%;
    border-radius: 30px;
    background: #fff;
    transition: 0.25s ease-in-out;
  }
  
  label span:nth-child(1){
    top: 0;
  }
  
  label span:nth-child(2){
    top: 8px;
  }
  
  label span:nth-child(3){
    top: 16px;
  }
  
  #checkbox-menu:checked + label span:nth-child(1){
    transform: rotate(-45deg);
    top: 8px;
  }
  
  #checkbox-menu:checked + label span:nth-child(2){
    opacity: 0;
  }
  
  #checkbox-menu:checked + label span:nth-child(3){
    transform: rotate(45deg);
    top:8px;
  }
  /* Fim botão hamburguer */


}

