#containerCalendario{
    display: flex;
    width: 99.1vw;
    height: 70vh;
    background-color: #08c56d;
    color: #fff;
    list-style: none;
    flex-wrap: wrap;
}

.calendario{
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 35vw;
    height: auto;
    align-items: center;
    background-color:#0a9454;
    padding: 30px;


}
.tituloCalendario{
    width: auto;
    margin:10px;
}
.diasSemana{
    display: flex;
    list-style: none;
    justify-content: center;
    align-items: center;
    
}
#diasSemana{
    display: flex;
    justify-content: center;
    width: 4vw;
    margin: 1vh;
    border: 1px solid #fff;
    font-size: 9pt;
    background-color: #fff;
    color: #08c56d;
    font-weight: bold;
    border-radius:  10px;
}
.primeiraSemana{
    display: flex;
    justify-content: center;
}
.primeiraSemana *{
    display:flex;
    list-style: none;
    margin: 1vh;
    width: 4vw;
    height: 7.5vh;
    border: 1px solid #fff;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    border-radius:  50%;


}
.segundaSemana{
    display: flex;
    justify-content: center;
}
.segundaSemana *{
    display:flex;
    list-style: none;
    margin: 1vh;
    width: 4vw;
    height: 7.5vh;
    border: 1px solid #fff;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    border-radius:  50%;

}
.terceiraSemana{
    display: flex;
    justify-content: center;
}
.terceiraSemana *{
    display:flex;
    list-style: none;
    margin: 1vh;
    width: 4vw;
    height: 7.5vh;
    border: 1px solid #fff;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    border-radius:  50%;


}
.quartaSemana{
    display: flex;
    justify-content: center;
}
.quartaSemana *{
    display:flex;
    list-style: none;
    margin: 1vh;
    width: 4vw;
    height: 7.5vh;
    border: 1px solid #fff;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    border-radius:  50%;



}
.quintaSemana{
    display: flex;
    justify-content: center;
}
.quintaSemana *{
    display:flex;
    list-style: none;
    margin: 1vh;
    width: 4vw;
    height: 7.5vh;
    border: 1px solid #fff;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    border-radius:  50%;

}
.fimdesemanaCalendario{
    border: 1px solid #08c56d;
}
.fimdiasCalendario{
    display: flex;
    justify-content: center;
    width: 4vw;
    margin: 10px;
    border: 1px solid #08c56d;
    font-size: 9pt;
    background-color: #08c56d;
    color: #fff;
    font-weight: bold;
    border-radius:  10px;
}
.feriadoCalendario{
    background-color: rgb(243, 208, 7);
    border: 1px solid rgb(243, 208, 7);
}
.eventosCalendario{
    display: flex;
    width: 60vw;
    height: 50vh;
    justify-content: center;
    list-style: none;
    flex-wrap: wrap;
    align-content: center ;

}

.eventosCalendario li{
    margin: 20px    
}

.eventosCalendario li h4{
    margin-bottom: 10px;
}
.textoEvento{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    width: 250px;
    height: 70px;
    background-color:#fff;
    font-weight: bold;
    color: #097543;
    border-top: 1px solid transparent;
    border-bottom: 1px solid transparent;
    transition-duration: 200ms ;
    padding: 30px 20px 30px 20px;
    /* box-shadow: 1px 4px 5px 5px #028045; */
}
.textoEvento:hover{

    transition: all 0.3;
    background-color: transparent;
    border:none;
    color:#097543;
    border-top: 1px solid #fff;
    border-bottom: 1px solid #fff;
    box-shadow: none;
    transform: scale( 1.1);
    background-color: #fff;
    box-shadow: 1px 4px 5px 5px #028045;
}

.textoCentroEvento{
    font-size: 12pt;
}
.diasPassado{
    color: #0aa35c;
    border: 1px solid #0aa35c;
}
.leiaEvento{
    position: relative;
    width: 23%;
    padding: 1px;
    left: 43%;
    border: 1px solid transparent;
    top: 47%;
    text-decoration: none;
    color: #0a9454;
    text-align: center;
    font-size: 11pt;
    font-weight: bold;

}
.leiaEvento:hover{
    transition: all 0.4s;
    background-color: #028045;
    color:#fff;
}


.tituloEvento{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 40px;
}
.classIconEvento{
    width: 30px;
    height: 30px;
    margin-right: 10px;
}
