} */
@media(max-width: 1650px){

    .tituloEvento{
        font-size: 22pt;
        margin-bottom: 40px
    }
    .textoEvento{
        margin: 5px ;
        height: 50px;
    }
}
@media(max-width: 1462px){
    .containerEventosPart{
        display: none;
    }
    .tituloEvento{
        display: none;
    }
    #containerCalendario{
        height: 63.8vh;
        justify-content: center;
        align-items: center;
    }
    .calendario{
        width: auto;
        height: auto;
    }
    .primeiraSemana *{
  
        height: 7vh;
    }
    .segundaSemana *{
  
        height: 7vh;
    }
    .terceiraSemana *{
  
        height: 7vh;
    }
    .quartaSemana *{
  
        height: 7vh;
    }
    .quintaSemana *{
  
        height: 7vh;
    }
    .fimdiasCalendario{
        width: 60px;
    }
    #diasSemana{
        width: 60px;
    }
    .classIconEvento{
        display: none;
    }
}

