    }
    .terceiraSemana *{
  
        height: 6vh;
    }
    .quartaSemana *{
  
        height: 6vh;
    }
    .quintaSemana *{
  
        height: 6vh;
    }
    #diasSemana{
        width: 55px;
        margin:0.6vh;
