.containeFooter{
    display: flex;
    width: 99.1vw;
    height:300px;
    background-color: rgb(0, 0, 0);
    color: #fff;
    flex-direction: column;
    align-items: center;

}
.footerGeral{
    display: flex;
    flex-direction: row;
    margin: 30px
}
.footerEsquerdo{
    display: flex;
    width: 60vw;
    flex-direction: column;
    justify-content: center;
}
.footerDireito{
    display: flex;
    width:25vw;
    flex-direction: column;


}
.footerDescricao{
    display: flex;
    height: 200px;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    text-align: justify;    
}
.footerSociais{
    display: flex;
    justify-content: center;
    align-items: center;

}
