.bannerText h1{
    color:#fff;
    font-size: 40pt;
}


.bannerText button{
    background: none;
    border: 1px solid #fff;
    width: 250px;
    padding: 11px;
    color:#fff;
    font-weight: bold;
    outline: none;
    font-size: 12pt;
    transition-duration: 200ms;
}

.bannerText button:hover{
    background-color: #0a9454;
    transform: scale(1.1);
}



.footerBanner{
    width: auto;
    height: 10px;
    background-color:#0a9454;
}
