.moveButton{
    position: absolute;
    left:0;
    display: flex;
    justify-content: space-evenly;
    align-items: flex-start;
}

.carrossel-mascara{
    width: 99.1vw; /* largura das imagens*/
    margin: 0 ;
    overflow: hidden;
    font-size: 0;
    }
    .carrossel-container {
    display: flex;
    width: 400vw;  /* Somar da largura de todas as imagens */
    transition: 1s ease;
    height: 337px; /* Altura das imagens */
    }
    .carrossel-container img, .carrossel-texto {
    width: 99.1;
    height: auto;
    display: inline-block;
    vertical-align:top;
    font-size: 16px;
    margin: 0 auto;
    }
 
    .slide {
    animation: slide 30s ease infinite; /* Velocidade*/
    }


    
    @keyframes slide {
        0% {
        transform: translateX(0%);
        }
        12.5% {
        transform: translateX(0%);
        }
        25% {
        transform: translateX(-25%);
        }
        37.5% {
        transform: translateX(-25%);
        }
        50% {
        transform: translateX(-50%);
        }
        62.5% {
        transform: translateX(-50%);
        }
        75% {
        transform: translateX(-75%);
        }
        87.5% {
        transform: translateX(-75%);
        }
        99% {
        transform: translateX(-75%);
        }
        100% {
        transform: translateX(0);
        }
    }

@media(max-width: 1100px){
    .imageBanner{
        display: none;
    
    }

    .elementoBanner {
        display: flex;
        width: auto;
        height: 100%;
        flex-direction: row;
        justify-content: center;
        flex-wrap: wrap;
        align-items: center;
    
    }
    .bannerText h1{
    color:#fff;
    }
    .bannerText p {
        width: 99.6vw;
    }
    
}

